










import { Vue, Component, Prop, Mixins, Watch } from "vue-property-decorator";
import TheLoginBox from "@/components/misc/TheLoginBox.vue";
import { notificationModule, authModule } from "@/store/modules";

@Component({
  components: {
    TheLoginBox
  }
})
export default class AdminLogin extends Mixins() {
  async mounted() {
    authModule.setLoaded(true);
  }
  async submit(data: { password: string; username: string }) {
    try {
      const response = await authModule.authenticateAdmin(data);
      const redirect = this.$route.query.redirect;
      console.log("redirect",redirect);
      this.$router.push(
        redirect ? (Array.isArray(redirect) ? (redirect[0] || "/") : redirect) : "/"
      );
    } catch (err) {
      console.log(err);
    }
  }
}
